import React from "react";
import { Link } from "react-router-dom";
import SportsCarousel from "../SportsCarousel";
import Heading from "../uiStyle/Heading";

const SportsNews = ({ dark }) => {
  return (
    <div className="row">
      <div className="col-12">
        <div className="sports">
          <Heading title="KINH T - CHNH TR" more={true} />
          <div className="row">
            <div className="col-md-6">
              <div className="single_post post_type3 mb30">
                <div className="post_img">
                  <Link to="/">
                    <img
                      src="https://xuatkhaulaodong.com.vn/images/2018/10/17/fukuoka-min.jpg"
                      alt="sportsbig1"
                    />
                  </Link>
                </div>
                <div className="single_post_text">
                  <div className="meta3">
                    <Link to="/">Kinh t - Chnh tr</Link>
                    <Link to="/">15/10/2023</Link>
                  </div>
                  <h4>
                    <Link to="/post1">
                      Tnh Fukuoka Nht Bn  T LNH cho lao ng Vit lp
                      nghip
                    </Link>
                  </h4>
                  <div className="space-10" />
                  <p className="post-p">
                    Fukuoka l vng t bnh yn nht ca Nht Bn do t chu
                    nh hng ca ng t, sng thn. C l y chnh l l do
                    m lao ng Vit nn la chn Fukuoka l im n  lp
                    nghip...
                  </p>
                </div>
              </div>
            </div>
            <div className="col-md-6">
              <div className="sports_carousel nav_style1">
                <SportsCarousel dark={dark ? dark : false} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default SportsNews;
